import React from "react";
//components
import { useNavigate } from "react-router-dom";
import { Image } from "react-bootstrap";

//MaterialUI
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import Box from '@mui/material/Box';
import Rating from '@mui/material/Rating';

//dependencias
import * as VideoServer from "../../services/videoServer";
import "../../styles/styles.css";

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(3),

    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    color: "white",

  },
  imagenCarousel:{
    minWidth:"100%",
    height:400,
    objectFit:"cover",
  },
  "@media (min-width: 1200px)": {
    /* imagen:{
      minWidth: 820,
      objectFit:'cover',

      height:400,

    },
    imagenCarousel:{
      width:1240,
      objectFit:'cover',
      height:400,

    }, */
  },
}));

/**
 * Componente para mostrar en el carousel del admin los videos con algunos campos.
 * @param {*} video
 * @returns Item de un video.
 */
const VideosItemAdmin = ({ video,...props }) => {
  const history = useNavigate();

  /**
   * Elimina el video seleccionado y recarga la pagina.
   * @param {pk} videoID 
   */
  const handleDelete = async (videoID) => {
    await VideoServer.DeleteVideo(videoID);
    window.location.reload();
  };
  const changeDuration = (duration) => {
    let tiempo = duration.split(':');
    let new_duration = "";
    if (tiempo) {
      if (tiempo[0] !== "00") {
        new_duration = tiempo[0] + "h";
      }
      if (tiempo[1] !== "00") {
        new_duration = " " + new_duration + " " + tiempo[1] + "min";
      }
      /* if (tiempo[2] !=="00") {
        new_duration = " "+new_duration+" "+tiempo[2]+"s";
      } */
    }
    return new_duration.trim(); // trim() para eliminar cualquier espacio en blanco adicional
  };

  const classes = useStyles();

  return (
    <div  id="contenedorItemVideo">
      <div className={"row "+classes.row}>
        <div className={"col-md-8  col-12 "+classes.imagen}>
          <Button onClick={() => props.verVideo(video.id)}>
            <Image
              src={"http://sccotview.com" + video.featured_image}
              className={classes.imagenCarousel}
            >
            </Image>
          </Button>
        </div>
        <div className="col-md-4 col-12 ">
        <div>
          <div className="d-flex justify-content-between mt-3 text-capitalize mb-3">
            <p className="text-light h5 ">{changeDuration(video.duration)}</p>
            <Box sx={{'& > legend': { mt: 2 }}}>
              <div className="bg-secondary d-inline pt-3 px-1 rounded-2">
                <Rating name="read-only" value={parseFloat(video.score)} precision={0.5} readOnly />
              </div>
            </Box>
          </div>
          <h3 className="h5 text-light text-capitalize text-start border-bottom border-white pb-2">{video.title_espanol}</h3>
          <span className="text-light text-start font-weight-light text-capitalize truncate-text-6">{video.description_esp}</span>
          {video.categorias.length > 0 && (
            <ul className="d-flex flex-wrap justify-content-start p-0 m-0 py-0  text-light">
              <span className="text-capitalize me-1">{video.categorias.length > 1 ? "CategorÃ­as" : "CategorÃ­a"} â†’ </span>
              {video.categorias.map(categoria => (
                <li key={categoria.id} className="list-group-item list-group-item-light px-2 rounded-4 mx-1"><small className="text-capitalize">{categoria.categoria}</small></li>
              ))}
            </ul>
          )}
          {video.autor.length > 0 && (
            <ul className="d-flex flex-wrap justify-content-start p-0 text-light" >
              
              <span className="text-capitalize">{video.autor.length > 1 ? "Autores" : "Autor" } â†’ </span>
              {video.autor.map(autor => (
                <p key={autor.id} className="px-2 rounded-4 "><small className="text-capitalize mb-2"> {/* â€¢ */} {autor.autor}</small></p>
              ))}
            </ul>
          )}
          </div>
          {/* <br></br>
          <div className="d-grid gap-2 col-6 mx-auto">
            <button
              onClick={() => history(`/updateVideo/${video.id}`)}
              className="ms-6 btn  btn-primary"
            >
              Actualizar a
            </button>
            <button
              onClick={() => video.id && handleDelete(video.id)}
              className="btn btn-outline-danger my-2"
            >
              Eliminar
            </button>
          </div> */}
        </div>
      </div>
    </div>
  );
};

export default VideosItemAdmin;
